@import url("https://fonts.googleapis.com/css?family=Lato:400,700|Lora|Playfair+Display:700i,900");

html,
body {
  margin: 0;
  width: 100%;
  font-family: "Lato", sans-serif;
}

header {
  color: #f9f9f9;
  background-color: #4a4a4a;
  text-align: center;
  padding: 10px;
  font-size: 1.8rem;
  /* z-index: 100; */
  height: 3rem;
  /* position: -webkit-sticky; Safari */
  /* position: sticky; */
  top: 0;
}

.quote-container {
  margin-bottom: 0px;
  display: flex;
  justify-content: space-between;
}

.center-align.filter {
  padding: 0px;
  line-height: 0px;
}

a {
  color: black !important;
}

h1,
h2,
p,
i,
a,
.first-letter,
.authorName a {
  color: rgba(0, 0, 0, 0.84);
  text-rendering: optimizeLegibility;
}

p {
  text-align: justify;
}

a {
  display: inline;
}

h1 {
  font-family: "Playfair Display", serif;
  font-size: 48px;
  text-align: left;
  margin-bottom: 8px;
}

h2 {
  font-family: "Lato", sans-serif;
  font-size: 30px;
  font-weight: 700;
  padding: 0;
  margin: 30px 0 -13px -1.883px;
  text-align: left;
  line-height: 34.5px;
  letter-spacing: -0.45px;
  margin-left: 20px;
}
h3 {
  font-family: "Lato", sans-serif;
  font-size: 25px;
  font-weight: 700;
  padding: 0;
  margin: 20px 0 20px 40px;
  text-align: left;
  line-height: 34.5px;
  letter-spacing: -0.45px;
  color: rgba(0, 0, 0, 0.68);
}
h4 {
  font-family: "Lato", sans-serif;
  font-size: 20px;
  font-weight: 700;
  padding: 0;
  margin: 20px 0 20px 60px;
  text-align: left;
  line-height: 34.5px;
  letter-spacing: -0.45px;
  color: rgba(0, 0, 0, 0.68);
}
.donnees {
  font-family: "Lato", sans-serif;
  font-size: 18px;
  font-weight: 400;
  padding: 0;
  margin: 10px 0 20px 60px;
  text-align: left;
  line-height: 34.5px;
  letter-spacing: -0.45px;
  color: rgba(0, 0, 0, 0.68);
}
p,
i {
  margin-top: 21px;
  font-family: "Lora";
  font-size: 21px;
  letter-spacing: -0.03px;
  line-height: 1.58;
}

a {
  text-decoration: underline;
}
.progress {
  margin: 0px;
}

blockquote {
  /* font-family: 'Playfair Display', serif; */
  display: flex;
  align-self: auto;
  font-size: 20px;
  font-style: italic;
  letter-spacing: -0.36px;
  line-height: 25px;
  /* overflow-wrap: break-word; */
  /* text-align: center; */
  margin: 40px 100px 3px 100px;
  /* percentag is better, the best is to use flexbox */
  /* text-align: center; */
  color: rgba(0, 0, 0, 0.68);
  padding: 0 0 0 5px;
}

code {
  font-size: 18px;
  background: rgba(0, 0, 0, 0.05);
  border-radius: 2px;
  padding: 3px 5px;
}

.highlighted {
  background: #7dffb3;
}

.first-letter {
  overflow-wrap: break-word;
  font-family: "Playfair Display", serif;
  font-size: 60px;
  line-height: 60px;
  display: block;
  position: relative;
  float: left;
  margin: 0px 7px 0 -5px;
}

.subtitle {
  font-family: "Lato", sans-serif;
  color: rgba(0, 0, 0, 0.54);
  margin: 0 0 24px 0;
}

/* ##################################################################################
########################################  LAYOUT  ###################################
##################################################################################### */
.flex-container {
  /* display: flex; */
  /* overflow-x: scroll; */
  /* width: 1000px; */
}

.containers {
  display: flex;
  /* width: 100px; */
  /* margin: 10px; */
  overflow-x: scroll;
  height: 400px;
}

.containers::-webkit-scrollbar {
  -webkit-appearance: none;
}

.containers::-webkit-scrollbar:vertical {
  width: 11px;
}

.containers::-webkit-scrollbar:horizontal {
  height: 11px;
}

.containers::-webkit-scrollbar-thumb {
  border-radius: 8px;
  border: 2px solid white; /* should match background, can't be transparent */
  background-color: rgba(0, 0, 0, 0.5);
}

img {
  width: 400px;
  height: auto;
}
